@font-face {
    font-family: 'Archivo Narrow';
    src: url('/fonts/archivo_narrow--bold.eot');
    src: url('/fonts/archivo_narrow--bold.eot?#iefix') format('embedded-opentype'),
         url('/fonts/archivo_narrow--bold.woff2') format('woff2'),
         url('/fonts/archivo_narrow--bold.woff') format('woff'),
         url('/fonts/archivo_narrow--bold.ttf') format('truetype'),
         url('/fonts/archivo_narrow--bold.svg#archivo_narrowbold') format('svg');
    font-weight: bold;
    font-style: normal;
}

